//-----------------FOOTER-----------------

.footer {
	background: url("../../img/footer_bg.jpg") center / cover no-repeat;
	height:     100%;

	&__wrapper {
		@include flex-sb;
		padding: 86px 0 126px 0;
	}

	&__review {
		display:         flex;
		flex-direction:  column;
		align-items:     center;
		justify-content: space-between;
		gap:             30px;

		&_img {
			border-radius: 50%;
			width:         96px;
			height:        96px;
		}

		&_text {
			font-size:   20px;
			line-height: 24px;
			text-align:  center;
			color:       $title-color;
			width:       100%;
			max-width:   360px;
		}
	}

	&__form {
		width:     100%;
		max-width: 364px;

		&_title {
			font-size:      24px;
			font-weight:    700;
			line-height:    167%;
			color:          $title-color;
			text-transform: uppercase;
			text-align:     center;
		}

		&_sub {
			display:       block;
			font-size:     18px;
			line-height:   167%;
			text-align:    center;
			color:         $title-color;
			margin-bottom: 32px;
		}

		&_input {
			width:                     256px;
			height:                    48px;
			background-color:          #e1e1e1;
			font-size:                 14px;
			line-height:               17px;
			padding:                   6px 22px;
			border-top-left-radius:    24px;
			border-bottom-left-radius: 24px;
			border:                    none;
			opacity:                   0.7;

			&:hover {
				outline: 1px solid $accent-color;
			}

			&:focus {
				outline: 1px solid $accent-color;
			}
		}

		&_btn {
			background-color:           $accent-color;
			border-bottom-right-radius: 24px;
			border-top-right-radius:    24px;
			border:                     none;
			color:                      #fff;
			padding:                    6px 22px;
			height:                     48px;

			&:hover {
				background-color: #db5757;
				outline:          1px solid $accent-color;
			}

			&:active {
				transform: scale(0.98);
			}
		}
	}
}

.form__input_wrapper {
	display: flex;
}
